.button {
  display: block;
  min-height: 40px;
  width: 280px;
  font-size: 16px;
  padding: 11px 0;
  font-weight: 400;
  line-height: 18px;
  color: $fancy-color;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  background-color: #68b738;
  box-sizing: border-box;
  cursor: pointer;
}
