body {
  position: relative;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  background-color: white; }

a {
  text-decoration: none;
  color: #000000;
  cursor: pointer; }

*,
*::after,
*::before {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  display: block;
  min-height: 40px;
  width: 280px;
