.main-nav {
  position: relative;
  /*min-height: 265px;/*если это убрать и поставить в html модификатор main-nav--closed исчезнет меню*/
  text-align: center;
  width: 320px;
  margin: 0 auto; }

.main-nav__toggle {
  position: absolute;
  top: 11px;
  right: 8px;
  z-index: 2;
  display: block;
  width: 50px;
  height: 50px;
  background-color: transparent;
  cursor: pointer;
  border: none; }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 18px;
  height: 18px;
  background-color: transparent;
  background-image: url("../img/icon-mobile-menu.svg");
  background-repeat: no-repeat; }

.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3); }

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 20px;
  left: 14px;
  width: 24px;
  height: 2px;
  background-image: url("../img/m-close.png");
  background-color: #000000; }

.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg); }

.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg); }

.main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3); }

